"use strict";
var router_1 = require('@angular/router');
var article_component_1 = require('./article.component');
var category_1 = require('./components/category');
var edit_1 = require('./components/edit');
var list_1 = require('./components/list');
var tag_1 = require('./components/tag');
var routes = [
    {
        path: '',
        component: article_component_1.Article,
        children: [
            { path: '', redirectTo: 'category', pathMatch: 'full' },
            { path: 'category', component: category_1.ArticleCategory },
            { path: 'post', component: edit_1.ArticleEdit },
            { path: 'edit/:article_id', component: edit_1.ArticleEdit },
            { path: 'list', component: list_1.ArticleList },
            { path: 'tag', component: tag_1.ArticleTag }
        ]
    }
];
exports.routing = router_1.RouterModule.forChild(routes);



